






import Vue from 'vue';
import { mapState } from 'vuex';

export default Vue.extend({
  name: 'DogsPicker',

  data() {
    return {
      selectedBreed: 'random',
    };
  },

  computed: {
    ...mapState({
      breeds: state =>
        (state as Record<string, Record<string, string>>).breeds.breeds,
    }),
  },

  watch: {
    async selectedBreed(value) {
      if (value) {
        this.$emit('input', value);
      }
    },
  },
});
